export default {

 /** 
  * 页面的初始数据 
  */
 data() { 
	 return {
		 url:"",
		 deviceId:"a",
		 urls:[],
		 urlsSum:0,
		 Urls:""
	 }
    

 }, 
 methods: {
   uploadImg(){
          let that = this;
          if(that.urlsSum<=9){
            uni.chooseImage({
				
              count: that.max || 3,
              sizeType: ['original', 'compressed'],
              sourceType: ['album', 'camera'],
              success: function (res) {
                // console.log(res);
                res.tempFilePaths.forEach(v=>{
                 that.urls.push(v);
                 that.urlsSum=that.urls.length;
                });
               // that.$emit("choosed",{all:that.urls,currentUpload:res.tempFilePaths});
            }
          })
          }else{
            uni.showToast({
                        title: '已经九张啦！',
                        icon: 'none',
                        duration: 2000
                        })
          }
          
        },
        previewImg(index){
          let that = this;
          uni.showActionSheet({
            itemList:["预览","删除"],
            success: function(res) {
              if(res.tapIndex === 0){
                uni.previewImage({
                  current:that.urls[index],
                  urls:that.urls
                });
              } else {
                that.urls.splice(index,1);
                that.urlsSum=that.urls.length;
                uni.showToast({
                        title: '删除成功',
                        icon: 'success',
                        duration: 1500
                        })
              }
            },
          });
        },
        historyUp:function(){
          let that = this;
          uni.request({
            url: that.url+'web/api/getSupplementList',
            header: {
                'content-type': 'application/json'
            },
            data:{
              deviceId:that.deviceId
            },
            method: 'get', 
            success: function(res){
          let str=res.data.data;
              str=JSON.parse(str);
              console.log(str);
              uni.showToast({
                        title: "请求成功",
                        icon: 'none',
                        duration: 2000
                        })
             
               
                
            },
            fail: function() {
                // fail
                // console.log("获取白名单组失败");
                uni.showToast({
                        title: 'fail,id:'+that.deviceId,
                        icon: 'none',
                        duration: 2000
                        })
            },
        });
        },
        upFile:function(){
          let l,that=this;
          l=that.urls.length;
          for(let i=0;i<l;i++){
            uni.uploadFile({
              url:that.url+"file/uploadSupplement",
							filePath:that.urls[i],
							name:"file",
							success:function(res){
                console.log(res);
                let a=JSON.parse(res.data).data;
                that.Urls=that.Urls+","+a;
                // console.log(that.Urls);
                // console.log(i,l);
                if(i+1==l){
                  setSupplement();
                }
							}
						})
          }

        function setSupplement(){
          let u = that.Urls.substr(1);
          console.log(u);
          uni.request({
            url: that.url+'web/api/setSupplement',
            header: {
                    "content-type": "application/json"
                },
            data:{
              deviceId:that.deviceId,
              url:that.Urls
            },
            method: "POST",  
            success: function(res){ 
                if(res.data.msg=="success"){
                 uni.showModal({
                     title: '上传成功',
                     content: '是否返回上一页',
                     success: function(res) {
                         if (res.confirm) {
                 			uni.navigateBack({
                 				delta:1
                 			})
                         } else if (res.cancel) {}
                     }
                 })
                }else{
                  uni.showToast({
                        title: '上传失败',
                        icon: 'none',
                        duration: 2000
                        })
                }
            },
            fail: function() {
                console.log("web/api/setSupplement失败");
                },
            });
        }
          
        }
    
  },
 onLoad: function (options) { 
   let that=this;
    that.url=that.$consturl;
    that.deviceId=uni.getStorageSync('deviceId');
 },
}
